*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-font: "Montserrat", Verdana, Arial, Helvetica, sans-serif;
  --secondary-font: "Inter", sans-serif;
  --main-text-cl: #000;
  --white-cl: #fff;
  --secondary-text-cl: rgba(0, 0, 0, .6);
  --default-btn-cl: #e86f54;
  --hover-btn-cl: #e25b41;
  --focus-btn-cl: #fa6f54;
  --icon-logo-cl: 0eE0E;
  --btn-radius: 38px;
  --anim-fast: 300ms ease;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

.link {
  color: currentColor;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section {
  padding-bottom: 60px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-bottom: 100px;
  }
}

.store__title, .subtitle {
  text-align: left;
  font-family: var(--main-font);
  text-transform: uppercase;
  color: var(--main-text-cl);
  align-items: center;
  gap: 14px;
  margin-bottom: 32px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
  display: flex;
}

.store__title:after, .subtitle:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--main-text-cl);
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .store__title, .subtitle {
    gap: 16px;
    margin-bottom: 60px;
    font-size: 44px;
  }
}

@media screen and (min-width: 1280px) {
  .store__title, .subtitle {
    margin-bottom: 80px;
  }
}

.store__title__item {
  text-align: left;
  font-family: var(--main-font);
  width: 100%;
  white-space: nowrap;
  color: var(--main-text-cl);
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
}

@media screen and (min-width: 768px) {
  .store__title__item {
    align-items: center;
    gap: 8px;
    font-size: 32px;
    display: inline-flex;
  }

  .store__title__item:after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: var(--main-text-cl);
    display: inline-block;
  }
}

@media screen and (min-width: 1280px) {
  .store__title__item {
    gap: 16px;
    margin-bottom: 4px;
    font-size: 36px;
  }
}

html {
  scroll-behavior: smooth;
}

.body {
  font-family: var(--main-font);
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 768px) {
