.btn {
  font-family: var(--main-font);
  min-width: 208px;
  min-height: 44px;
  color: var(--white-cl);
  border-radius: var(--btn-radius);
  transition: background-color var(--anim-fast);
  background-color: #e86f54;
  border: none;
  margin: 0 auto;
  padding: 10px 35px;
  font-size: 20px;
  font-weight: 500;
  line-height: .857;
  display: block;
}

.btn:hover {
  background-color: var(--hover-btn-cl);
}

.btn:focus {
  background-color: var(--focus-btn-cl);
}

@media screen and (min-width: 768px) {
  .btn {
    min-width: 280px;
    min-height: 64px;
    color: var(--white-cl);
    padding: 20px 43.5px;
    font-size: 28px;
    font-weight: 500;
    line-height: 1.2;
  }
}

.header {
  width: 100vw;
  min-height: 60px;
  background-color: var(--white-cl);
  z-index: 1000;
  padding-top: 16px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.header:has(.mobile-menu__btn.is-active) {
  background-color: rgba(0, 0, 0, 0);
}

.mobile-menu__btn.is-active ~ .logo {
  opacity: 0;
}

.logo__icon {
  width: 54px;
  height: 42px;
  margin-right: 5px;
}

@media screen and (min-width: 768px) {
